@import url(https://fonts.googleapis.com/css?family=Noto+Sans+KR&display=swap);
html {
    height: 100%;
    box-sizing: border-box;
}

span {
    text-transform: capitalize;
}

body {
    background-color: #425866;
    -webkit-animation: 100s scroll infinite linear;
    animation: 100s scroll infinite linear;
    font-family: 'Noto Sans KR', sans-serif;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;

}

.text-center {
    text-align: center;
}

h1.body-head {
    color: #fff;
    font-size: 3em;
    text-align: center;

    text-decoration: underline;
    letter-spacing: 4px;
    /*https://css-tricks.com/3d-text-tower/*/
    text-shadow: 1px 1px 1px red,
    1px 1px 1px red,
    1px 1px 1px red;

    /*font-family: 'Faster One', cursive;*/
    font-family: 'Monoton', cursive;

}

/* Footer */

.footer {
    clear: both;
    position: relative;
    z-index: 10;
    margin-top: -3em;
    right: 0;
    bottom: 0;
    left: 0;
    padding: 20px 5px 20px 5px;
    text-align: center;
    height: 100px;
    background-color: #232b2b;
    color: white;
    border-top: 2px solid white;
}

.footer a {
    color: white;
    text-decoration: none;
    border-bottom: 1px solid rgba(255, 255, 255, .7);;
}

.footer a:hover {
    border-bottom: 1px solid rgba(255, 255, 255, 1);;
}

.footer p {
    opacity: 0.75;
    font-size: 16px;
}

.footerCopyRight {
    margin-top: 5px;
}

.App {
    text-align: center;
}

.header {
    font-size: 40px;
    color: white;
}

/* Movie Card CSS */

.name {
    font-size: 25px;
    font-weight: bold;

}

.title {
    color: gray;
    font-size: 15px;
    font-weight: bold;
    padding-left: 10px;
}

.card {
    padding: 10px;
    border-radius: 15px;
    background-color: white;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
    -webkit-transition: 0.3s;
    transition: 0.3s;
    width: 100%;
    height: auto;
    border: 5px solid black;
    margin: 15px;
    display: inline-block;
    box-sizing: border-box;
}

.card:hover {
    box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2);
}

img {

    max-width: 100%;
    max-height: 100%;
    width: 100%;
    height: 270px;
}

.filters {
    margin-left: 30px;
}

.container {
    padding: 2px 16px;
}

.button {
    background-color: #4CAF50; /* Green */
    font-weight: bold;
}

.red {

    background-color: #c23232;
    border-bottom: 5px solid #c23232;
}

.blue {
    background-color: #00BCD4;
    border-bottom: 5px solid #00BCD4;
}

/* One column grid of 300px blocks when screen smaller than 600px */
@media screen and (max-width: 400px) {
    .card {
        background-color: #f1f1f1;
        box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
        -webkit-transition: 0.3s;
        transition: 0.3s;
        width: 100%;
        border-radius: 5px;
        margin: 10px;
        display: inline-block;
    }

    body {
        font-size: 10px !important;

    }
}

@media screen and (max-width: 600px) and (min-width: 401px) {
    .card {
        background-color: #f1f1f1;
        box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
        -webkit-transition: 0.3s;
        transition: 0.3s;
        width: 100%;
        border-radius: 5px;
        margin: 5px;
        display: inline-block;
    }

    body {
        font-size: 10px !important;

    }
}

@media screen and (max-width: 1024px) and (min-width: 601px) {
    .card {
        background-color: #f1f1f1;
        box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
        -webkit-transition: 0.3s;
        transition: 0.3s;
        width: 100%;
        border-radius: 5px;
        margin: 10px;
        display: inline-block;
    }

    body {
        font-size: 10px !important;

    }
}

.poster {
    border-radius: 50%;
    height: 100px;
    width: 120px;
}

.avatar-circle {
    height: 100px;
    width: 120px;
    text-align: center;
    border-radius: 50%;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
}

.pink {
    background-color: palevioletred;
}

.blue {
    background-color: deepskyblue;
}

.green {
    background-color: limegreen;
}

.purple {
    background-color: mediumorchid;
}

.brown {
    background-color: goldenrod;
}

.initials {
    position: relative;
    top: 20px; /* 25% of parent */
    font-size: 4em; /* 50% of parent */
    line-height: 50px; /* 50% of parent */
    color: #fff;
    font-weight: bold;
}

.flex {
    display: flex;
    flex-direction: row;
}

.card-name-wrapper {
    display: table-cell;
    vertical-align: middle;
    height: 100px;
    /*line-height: 20px;*/
    padding-left: 20px;
    text-align: left;
}

.stats {
    margin-top: 12px;
    text-align: right;
    right: 0;
    width: 100%;
    margin-right: 22px;
    line-height: 20px;
    overflow: visible;
    margin-left: -30px;
    z-index: 10000;
}

.impressions {
    font-size: 20px;
    color: darkorange;
}

.activity {

    color: silver;
}

.conversions {
    font-size: 20px;

    color: #3090C7;
}

.revenue {
    font-size: 25px;
    margin-top: 10px;
    font-weight: 900;

    color: #32CD32
}


.chart{
    z-index: 100;
}
.chart-div{
    min-height: 100px;
    min-width: 80%;
}
